import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useParams } from 'react-router-dom';
import { Bar } from 'react-chartjs-2';

import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
} from 'chart.js';

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

const FocusDetailPage = () => {
  const [summary, setSummary] = useState(null);
  const { date } = useParams();  //   date 
  const [timelineData, setTimelineData] = useState(null);
  const [blinkGraphData, setBlinkGraphData] = useState(null);


  useEffect(() => {
  axios
      .get(`https://learningas.shop/focus/summary/?date=${date}`)
      .then((res) => {
        setSummary(res.data);
      })
      .catch((err) => {
        console.error("   ", err);
      });

      //  timeline 
  axios
    .get(`https://learningas.shop/focus/timeline/?date=${date}`)
    .then((res) => {
      const raw = res.data.timeline;
      setTimelineData({
        labels: raw.map((r) => r.time),
        datasets: [
          {
            label: ' ',
            data: raw.map((r) => r.absent),
            backgroundColor: 'rgba(255, 99, 132, 0.6)',
          },
          {
            label: ' ',
            data: raw.map((r) => r.zoneout),
            backgroundColor: 'rgba(255, 206, 86, 0.6)',
          },
        ],
      });
    })
    .catch((err) => console.error("timeline  ", err));
  axios
  .get(`https://learningas.shop/focus/blink_summary/?date=${date}`)
  .then((res) => {
    const timeline = res.data.timeline;

    setBlinkGraphData({
        labels: timeline.map((d) =>
          `${d.time} - ${d.drowsy ? ' ' : ' '}`
        ),
        datasets: [
          {
            label: '   (60 )',
            data: timeline.map((d) => d.blink_count),
            backgroundColor: timeline.map((d) =>
              d.drowsy ? 'rgba(255, 99, 132, 0.6)' : 'rgba(75, 192, 192, 0.6)'
            )
          }
        ]
      });
    })
  .catch((err) => console.error("blink summary  ", err));


  }, [date]);


  const formatTime = (seconds) => {
    const min = Math.floor(seconds / 60);
    const sec = seconds % 60;
    return `${min} ${sec}`;
  };

  return (
    <div style={{ padding: '20px' }}>
      <h2> {date}  </h2>
      {summary ? (
        <div>
          <p>   {summary.heart_rate_stable ? "  " : " "}</p>
          <p>    {summary.study_time_min}</p>
          <p>    {summary.blink_count > 100 ? "" : ""}</p>
          <p>    {summary.present ? "" : "   "}</p>
          <p>   {formatTime(summary.zoneout_time_sec)}</p>
          <p>   : {summary.focus_score}</p>
          {/*      */}
    {timelineData && (
      <div style={{ marginTop: "40px" }}>
        <h3>   </h3>
        <Bar
          data={timelineData}
          options={{
            responsive: true,
            plugins: {
              legend: { position: 'top' },
            },
            scales: {
              y: {
                beginAtZero: true,
                title: { display: true, text: ' ()' },
              },
              x: {
                title: { display: true, text: '10  ' },
              },
            },
          }}
        />
      </div>
    )}

    {blinkGraphData && (
  <div style={{ marginTop: "40px" }}>
    <h3> 60    </h3>
    <Bar
      data={blinkGraphData}
      options={{
        responsive: true,
        plugins: {
          legend: { position: 'top' },
          title: {
            display: true,
            text: '  +  ',
          },
        },
        scales: {
          y: {
            beginAtZero: true, 
            suggestedMax: 10,
            title: {
              display: true,
              text: ' ',
            },
          },
          x: {
            title: {
              display: true,
              text: ' (60 )',
            },
          },
        },
      }}
    />
  </div>
)}





        </div>
      ) : (
        <p>  ...</p>
      )}
    </div>
  );
};

export default FocusDetailPage;
